.main-login-page{
    width: 40rem;
    display: flex;
    flex-direction: column;
    gap: 2.6rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    padding: 2.6rem;
    border: 1px solid #DFDCDC;
    /* font-size: 16px; */
 }   
 .login-heading{
     width: 100%;
     display: flex;
     flex-direction: column;
     gap: 2rem;
 }
 .login-heading>p>span{
     font-size: 2.1rem;
 }
 .login-heading>h1{
     font-size: 3.1rem;
     font-weight: 400;
 }
 .google-btn{
     display: flex;
     align-items: center;
     gap: 1rem;
     padding: 0.5rem 7rem 0.5rem 7rem;
     font-size: 1.6rem;
     border: 1px solid #DFDCDC;
     background-color: #fff;
     cursor: pointer;
 }
 .google-logo{
     width: 2.6rem;
     height: 2.6rem;
     border-radius: 5.6rem;
 }
 .email-login{
     width: 100%;
     display: flex;
     align-items: center;
     justify-content: space-between;
     font-size: 10px;
 }
 .left-line{
     width: 7rem;
     height: 1px;
     border-radius: 7px;
     background-color: #DFDCDC;
 }
 .login-details>h3{
     font-size: 1.6rem;
     font-weight: 600;
     color: #B7B7B7;
 }
 .login-details>input{
     padding: 1.1rem;
     font-size: 1.6rem;
     border: 1px solid rgba(71, 68, 68, 0.315);
     border-radius: 7px; 
     line-height: 2;
 }
 .login-details:nth-child(5){
     /* padding-left:50%; */
     background-color: aqua;
 }
 .colomn-flex{
     display: flex;
     flex-direction: column;
     gap: 1.6rem; 
 }
 .rember-checkbox{
     display: flex;
     justify-content: space-between;
     align-items: center;
 }
 .forgot-btn{
     position: relative;
     cursor: pointer;
     color: #8187DC;
 }
 .eye-logo{
     position: absolute;
     top: -50px;
     left: 82px;
     color: #222;
     cursor: pointer;
     font-size: 20px;
 }
 #checkbox{
     display: flex;
     align-items: center;
     gap: 5px;
     font-size: 14px;
 }
 .login-btn{
     padding: 1.6rem 5.6rem 1.6rem 5.6rem;
     font-size: 1.6rem;
     border: none;
     cursor: pointer;
     background-color: #8187DC;
     color: #fff;
     border-radius: 50px;
 }
 .create-account{
     display: flex;
     align-items: center;
     justify-content: space-around;
 }
 #create-account{
     display: flex;
     align-items: center;   
     gap: 5px;
 }
 .create-icon{
     color: #8187DC;
     cursor: pointer;
 }

 p {
    font-size: 14px;
 }