@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600&display=swap');

.links {
    display: flex;
    flex-direction: row;
    gap: 1rem;
    list-style: none;
    text-decoration: none;
}
li, button{
    cursor: pointer;
}
::placeholder {
    background-color: #fff;
    color: #B7B7B7;
}

::selection {
    background-color: #8187DC;
    color: #fff;
}

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-thumb {
    background-color: #8187DC;
    border-radius: 5rem;
    overflow: hidden;
}

button,
input {
    font-family: inherit;
}